.card {
  position: absolute;
  top: 155px;
  width: 120px;
  height: 120px;
  transform: rotate(45deg);
  cursor: pointer;
  transition: all 0.2s ease-in-out;
  box-shadow: 2px 2px 10px rgba(0, 0, 0, 0.5);
}
.card.enter-active {
  animation: up 0.5s ease-in-out;
}
.card:hover {
  top: 142px;
  width: 150px;
  height: 150px;
}
.card.active {
  top: 142px;
  width: 150px;
  height: 150px;
}
.card.active.enter-active {
  animation: activeUp 0.5s ease-in-out;
}
.card:nth-child(2) {
  left: var(--dis);
}
.card:nth-child(3) {
  left: calc(var(--dis) * 2);
}
.card:nth-child(4) {
  left: calc(var(--dis) * 3);
}
.card:nth-child(5) {
  left: calc(var(--dis) * 4);
}
.card:nth-child(6) {
  left: calc(var(--dis) * 5);
}
.card:nth-child(7) {
  left: calc(var(--dis) * 6);
}
.card:nth-child(8) {
  left: calc(var(--dis) * 7);
}
@keyframes up {
  from {
    top: 205px;
  }
  to {
    top: 155px;
  }
}
@keyframes activeUp {
  from {
    top: 205px;
  }
  to {
    top: 142px;
  }
}
@-webkit-keyframes up {
  from {
    top: 205px;
  }
  to {
    top: 155px;
  }
}
@-webkit-keyframes activeUp {
  from {
    top: 260px;
  }
  to {
    top: 142px;
  }
}
